<template>
  <div :class="`${prefixCls}-status`">
      <section v-if="audit.audit_at">
        <section class="gray"  v-if="audit.status === 1">
          
        </section>
        <section class="red" v-else>
          
        </section>
      </section>
      <section @click="showOperations(audit)" class="green" v-else>
        
      </section>
  </div>
</template>
<script>
/**
 * 
 */

const prefixCls = "msgList";
export default {
  name: "groupPostCommentAuditStatus",
  props: ["audit"],
  data: () => ({
    prefixCls
  }),
  methods: {
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    accept() {
      const { currentItem = {} } = this;
      const { group_id: group = 0, member_id: member = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(
          `/plus-group/groups/${group}/members/${member}/audit`,
          {
            status: 1
          },
          {
            validateStatus: s => s === 201
          }
        )
        .then(({ data }) => {
          this.audit.audit_at = 1;
          this.audit.status = 1;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    reject() {
      const { currentItem = {} } = this;
      const { group_id: group = 0, member_id: member = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(
          `/plus-group/groups/${group}/members/${member}/audit`,
          {
            status: 2
          },
          {
            validateStatus: s => s === 201
          }
        )
        .then(({ data }) => {
          this.audit.audit_at = 1;
          this.audit.status = 2;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    showOperations(audit) {
      if (audit.feed === null) {
        this.$Message.error(",");
        return false;
      }
      this.currentItem = audit;
      const vm = this;
      this.$Modal.info({
        title: false,
        render(h) {
          return h("div", {}, [
            h(
              "button",
              {
                on: {
                  click: vm.accept
                }
              },
              ""
            ),
            h(
              "button",
              {
                on: {
                  click: vm.reject
                }
              },
              ""
            )
          ]);
        }
      });
    }
  }
};
</script>
<style lang="less" src="../style.less">
</style>



