<template>
  <div :class="`${prefixCls}-status`">
    <section v-if="audit.comment">
      <section v-if="audit.expires_at">
        <section class="gray"  v-if="audit.state === 1">
          
        </section>
        <section class="red" v-else>
          
        </section>
      </section>
      <section @click="showOperations(audit)" class="green" v-else>
        
      </section>
    </section>
    <section class="red" v-if="!audit.comment ">
      
    </section>
  </div>
</template>
<script>
/**
 * 
 */

const prefixCls = "msgList";
export default {
  name: "groupPostCommentAuditStatus",
  props: ["audit"],
  data: () => ({
    prefixCls
  }),
  methods: {
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    accept() {
      const { currentItem = {} } = this;
      const { target: commentId = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(`/plus-group/pinned/comments/${commentId}/accept`, {
          validateStatus: s => s === 201
        })
        .then(({ data }) => {
          this.audit.expires_at = 1;
          this.audit.status = 1;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    reject() {
      const { currentItem = {} } = this;
      const { target: commentId = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .delete(`/plus-group/pinned/comments/${commentId}/reject`, {
          validateStatus: s => s === 201
        })
        .then(({ data }) => {
          this.audit.expires_at = 1;
          this.audit.status = 2;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    showOperations(audit) {
      if (audit.feed === null) {
        this.$Message.error(",");
        return false;
      }
      this.currentItem = audit;
      const vm = this;
      this.$Modal.info({
        title: "",
        render(h) {
          return h("div", {}, [
            h(
              "button",
              {
                on: {
                  click: vm.accept
                }
              },
              ""
            ),
            h(
              "button",
              {
                on: {
                  click: vm.reject
                }
              },
              ""
            )
          ]);
        }
      });
    }
  }
};
</script>
<style lang="less" src="../style.less">
</style>



